import React from "react";
import Home from "./Home";
import Composer from "./Composer";
import Spaces from "./Spaces";
import Dogfooding from "./Dogfooding";
import { PrivatePage } from "./Components";
import {
  BrowserRouter as Router,
  Link,
  Route,
  Switch,
  } from 'react-router-dom';

export const PRIVATE_PAGE_KEY = process.env.REACT_APP_PRIVATE_PAGE_KEY

const HomePage = () => <Home />;
const ComposerPage = () => <PrivatePage component={Composer} />;
const SpacesPage = () => <PrivatePage component={Spaces} />;


function App() {
  return (
    <Router>
      <div>
        <Switch>
          <Route exact path="/" component={HomePage} />
          <Route path="/composer" component={ComposerPage} />
          <Route path="/spaces" component={SpacesPage} />
          <Route render={() => <h1>Page not found</h1>} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;